.how-we-do {
  padding: 46px 0 121px;

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: var(--grid-gap);
    counter-reset: section;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    grid-column: 3 span;

    &::before {
      counter-increment: section;
      content: '0' counter(section);
      margin-bottom: 15px;
      font-weight: 700;
      font-size: 46px;
      line-height: 130%;
      color: #d7dadd;
    }

    &:not(:last-child) {
      &::after {
        content: '';
        position: absolute;
        top: 28px;
        right: 0;
        width: 75%;
        height: 1px;
        background-image: url('../img/how-we-do-line.svg');
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        @include tablet {
          display: none;
        }
      }
    }

    @include tablet {
      grid-column: 6 span;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-ebonyClay);
  }

  &__descr {
    margin: 0;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-stormGray);
  }
}
