.post {
  &__cover {
    display: block;
    margin-top: -79px;
    width: 100%;
    object-fit: cover;
  }

  &__wrapper {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 839px;
  }

  &__share {
    @include flex-v-center;

    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-ebonyClay);

    span {
      margin-right: 10px;
    }

    svg {
      fill: var(--color-stormGray);
      opacity: 1;
    }
  }

  &__content {
    padding-top: 80px;
    padding-bottom: 62px;

    * {
      margin-bottom: 25px;
      font-size: 16px;
      line-height: 160%;
      color: var(--color-gray);

      &:last-child {
        margin-bottom: 0;
      }
    }

    h2 {
      margin: 0;
      margin-bottom: 25px;
      font-weight: 700;
      font-size: 20px;
      line-height: 150%;
      color: var(--color-ebonyClay);
    }

    h3 {
      margin: 0;
      margin-bottom: 25px;
      font-weight: 700;
      font-size: 18px;
      line-height: 150%;
      color: var(--color-ebonyClay);
    }

    p {
      margin: 0;
      margin-bottom: 28px;
      font-size: 16px;
      line-height: 160%;
      color: var(--color-gray);
    }

    blockquote {
      position: relative;
      margin: 0;
      margin-bottom: 29px;
      padding: 20px 58px;
      padding-right: 5px;
      font-weight: 700;
      font-size: 20px;
      line-height: 150%;
      color: var(--color-ebonyClay);

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 20px;
        width: 28px;
        height: 20px;
        background-image: url('../img/braces.svg');
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
      }

      @include mobile {
        padding: 0;
        padding-top: 50px;
      }
    }

    ul {
      margin: 0;
      margin-bottom: 26px;
      padding: 0;
      list-style-type: none;
      font-size: 16px;
      line-height: 160%;
      color: var(--color-gray);

      li {
        position: relative;
        padding-left: 32px;

        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 5px;
          width: 16px;
          height: 16px;
          background-image: url('../img/check-list.svg');
          background-position: center;
          background-size: cover;
          background-repeat: no-repeat;
        }

        &:not(:last-child) {
          margin-bottom: 3px;
        }
      }
    }
  }
}
