.positions-content {
  padding-top: 121px;
  padding-bottom: 60px;

  &__left {
    grid-column: 7 span;
  }

  &__right {
    display: flex;
    justify-content: flex-end;
    grid-column: 5 span;
  }
}

.positions-list {
  &__item {
    &:not(:last-child) {
      margin-bottom: 23px;
    }
  }
}

.positions-item {
  @include flex-all-sb;

  padding: 32px 40px;
  border: 1px solid #e5e8ed;
  border-radius: 4px;
  background-color: var(--color-light);

  &__meta {
    margin-bottom: 8px;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-stormGray);
  }

  &__city {
    position: relative;
    margin-right: 22px;
    padding-left: 23px;
    background-image: url('../img/city.svg');
    background-position: left center;
    background-size: 16px 16px;
    background-repeat: no-repeat;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: -10px;
      width: 1px;
      height: 12px;
      background-color: currentColor;
      transform: translateY(-50%);
    }
  }

  &__time {
    padding-left: 23px;
    background-image: url('../img/time.svg');
    background-position: left center;
    background-size: 16px 16px;
    background-repeat: no-repeat;
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-ebonyClay);
  }

  &__left {
    @include tablet {
      margin-bottom: 20px;
    }
  }

  &__link {
    display: inline-block;
    padding: 11px 30px;
    font-size: 14px;
    letter-spacing: 0.5px;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--color-light);
    }

    &:active {
      color: var(--color-active);
    }
  }

  @include tablet {
    flex-direction: column;
    align-items: flex-start;
  }
}

.positions-looking {
  width: 390px;

  &__title {
    margin: 0;
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--color-ebonyClay);
  }

  &__descr {
    margin: 0;
    margin-bottom: 48px;
    font-size: 18px;
    line-height: 150%;
    color: var(--color-gray);
  }

  &__btns {
    @include tablet {
      display: flex;
      flex-direction: column;

      .positions-looking__btn--subs {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
  }

  &__btn {
    &--subs {
      margin-right: 22px;
      padding-left: 44px;
      padding-right: 44px;
      transition: color 0.3s ease-in-out;

      &:focus {
        outline: 2px solid var(--color-primary);
        outline-offset: 2px;
      }

      &:hover {
        color: var(--color-light);
      }

      &:active {
        color: var(--color-active);
      }
    }

    &--send {
      padding-left: 54px;
      padding-right: 54px;
    }
  }
}
