.achieve {
  padding-top: 121px;
  padding-bottom: 117px;
  background-image: url('../img/achieve-bg.png');
  background-position: right -9px bottom -528px;
  background-repeat: no-repeat;
  background-color: var(--color-light);

  &__item {
    @include flex-column-center;

    grid-column: 3 span;
    padding-top: 69px;
    background-position: top center;
    background-size: 48px 48px;
    background-repeat: no-repeat;

    &--reccom {
      background-image: url('../img/like.svg');
    }

    &--renov {
      background-image: url('../img/paint.svg');
    }

    &--spec {
      background-image: url('../img/helmet.svg');
    }

    &--finish {
      background-image: url('../img/pant.svg');
    }

    @include tablet {
      grid-column: 6 span;
    }
  }

  &__value {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 46px;
    line-height: 130%;
    color: var(--color-primary);

    @include mini-desktop {
      font-size: 36px;
      line-height: 1.3;
    }
  }

  &__text {
    font-size: 14px;
    line-height: 150%;
    text-align: center;
    color: var(--color-ebonyClay);
  }

  &__list {
    margin-bottom: 260px;
  }

  &__content {
    display: flex;
    align-items: flex-start;

    @include tablet {
      display: block;
    }
  }

  &__image {
    @include mini-desktop {
      max-width: 45%;
    }

    @include tablet {
      max-width: 100%;
    }
  }

  &__info {
    position: relative;
    left: -30px;
    top: -130px;
    z-index: 10;
    flex-shrink: 0;
    padding: 80px;
    padding-right: 40px;
    padding-bottom: 58px;
    border-radius: 4px;
    width: 735px;
    box-shadow: var(--main-shadow);
    background-color: var(--color-light);

    @include mini-desktop {
      left: 0;
      top: 0;
      width: 55%;
    }

    @include tablet {
      width: 100%;
    }

    @include mobile {
      padding: 15px;
    }
  }
}

.quote {
  margin: 0;

  &__content {
    position: relative;
    margin-bottom: 31px;
    padding: 10px 55px;

    &::before {
      content: '';
      position: absolute;
      left: -3px;
      top: 10px;
      width: 28px;
      height: 20px;
      background-image: url('../img/braces.svg');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;

      @include mobile {
        left: 0;
      }
    }

    p {
      margin: 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 150%;
      color: var(--color-ebonyClay);

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    @include mobile {
      padding: 0;
      padding-top: 40px;
      padding-bottom: 20px;
    }
  }

  &__footer {
    padding: 20px 56px;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-stormGray);
    background-image: url('../img/signature.svg');
    background-position: right 66px center;
    background-size: 100px 68px;
    background-repeat: no-repeat;

    @include mobile {
      padding-bottom: 120px;
      background-position: bottom 30px center;
    }
  }

  &__author {
    display: block;
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-ebonyClay);
  }
}
