.portfolio-item {
  display: flex;
  flex-direction: column;
  border-radius: 4px;
  box-shadow: 0 80px 80px -20px rgba(154, 156, 165, 0.08),
    0 30px 24px -10px rgba(154, 156, 165, 0.05), 0 12px 10px -6px rgba(154, 156, 165, 0.04),
    0 4px 4px -4px rgba(30, 33, 44, 0.03);
  overflow: hidden;
  background-color: var(--color-light);
  transition: transform 0.3s ease-in-out;

  &__content {
    @include flex-column-center;

    padding: 16px 10px 23px;
  }

  &__title {
    margin: 0;
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-ebonyClay);

    @include mini-desktop {
      font-size: 18px;
    }
  }

  &__descr {
    display: inline-block;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-stormGray);
  }

  &__btn {
    margin-top: 24px;
    margin-bottom: -68px;
    padding: 13px 31px;
    border: 1px solid var(--color-primary);
    border-radius: 4px;
    font-weight: 700;
    font-size: 14px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--color-primary);
    transition: 0.3s ease-in-out;
    will-change: margin;

    @include small-tablet {
      margin-bottom: 0;
    }
  }

  &:focus {
    outline: 2px solid var(--color-primary);
    outline-offset: 2px;
  }

  &:hover {
    .portfolio-item__btn {
      margin-bottom: 0;
    }
  }
}
