.services-benefits {
  padding-top: 80px;
  background-color: var(--color-ebonyClay);

  &__title {
    color: var(--color-light);
  }

  &__btn {
    width: 390px;
    transform: translateY(13px);
  }

  &__centered {
    margin-bottom: -13px;
  }

  &__description {
    font-size: 18px;
    line-height: 150%;
  }

  &__list {
    --mission-gap: 94px;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--mission-gap) * 2);
    margin-bottom: 27px;

    @include tablet {
      --mission-gap: 30px;
    }

    @include small-tablet {
      --mission-gap: 10px;
    }

    @include mobile {
      display: block;
    }
  }

  &__item {
    position: relative;
    padding-top: 72px;
    text-align: center;
    background-position: top center;
    background-size: 48px 48px;
    background-repeat: no-repeat;

    &:not(:last-child) {
      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: calc(-1 * var(--mission-gap));
        width: 1px;
        height: 100%;
        background-image: url('../img/divider.svg');
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        @include small-tablet {
          display: none;
        }
      }
    }

    &--terms {
      background-image: url('../img/terms.svg');
    }

    &--workers {
      background-image: url('../img/workers.svg');
    }

    &--control {
      background-image: url('../img/control.svg');
    }

    @include mobile {
      font-size: 15px;
      line-height: 1.3;

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-light);
  }

  &__descr {
    margin: 0;
    margin-bottom: 62px;
    line-height: 160%;
    color: var(--color-stormGray);
  }
}
