.recent-news {
  padding: 79px 0 120px;
  background-color: var(--color-ligh);

  &__title {
    margin-bottom: 59px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(2, 255px);
    gap: var(--grid-gap);
    margin-bottom: 80px;

    @include tablet {
      grid-template-rows: initial;
    }

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__item {
    grid-column: 5 span;

    &:first-child {
      grid-column: 7 span;
      grid-row: 2 span;

      @include tablet {
        grid-column: 12 span;
        grid-row: auto;
      }

      @include mobile {
        grid-column: 4 span;
      }
    }

    @include tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }
}
