.about-mission {
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: var(--color-athens);

  &__list {
    --about-mission-gap: 94px;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--about-mission-gap) * 2);

    @include tablet {
      --about-mission-gap: 30px;
    }

    @include small-tablet {
      --about-mission-gap: 10px;
    }

    @include mobile {
      display: block;
    }
  }

  &__item {
    position: relative;
    padding-top: 72px;
    text-align: center;
    background-position: top center;
    background-size: 48px 48px;
    background-repeat: no-repeat;

    &:not(:last-child) {
      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: calc(-1 * var(--about-mission-gap));
        width: 1px;
        height: 100%;
        background-image: url('../img/divider.svg');
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
      }
    }

    &--quality {
      background-image: url('../img/quality.svg');
    }

    &--safety {
      background-image: url('../img/safety.svg');
    }

    &--comfort {
      background-image: url('../img/comfort.svg');
    }

    @include mobile {
      font-size: 15px;
      line-height: 1.3;

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-ebonyClay);
  }

  &__descr {
    margin: 0;
    line-height: 160%;
    color: var(--color-stormGray);
  }
}
