.contacts-section {
  padding: 80px 0;
  background-image: url('../img/contacts-bg.jpg');
  background-color: var(--color-ebonyClay);
  @include bg-cover;

  &__form {
    background-color: var(--color-light);
  }

  &__container {
    display: flex;
    justify-content: flex-end;
  }
}

.contacts-form {
  @include flex-column-start;

  padding: 48px 40px;
  border: 1px solid #e5e8ed;
  border-radius: 4px;
  max-width: 495px;
  box-shadow: 0 60px 80px -20px rgba(30, 33, 44, 0.16), 0px 26px 24px -10px rgba(30, 33, 44, 0.1),
    0 12px 10px -6px rgba(30, 33, 44, 0.08), 0px 4px 4px -4px rgba(30, 33, 44, 0.05);

  &__title {
    margin: 0;
    margin-bottom: 24px;
    width: 100%;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    text-align: center;
    color: var(--color-ebonyClay);
  }

  &__field {
    margin-bottom: 20px;
  }

  &__checkbox {
    margin-bottom: 24px;
  }

  &__btn {
    padding: 10px 30px;
    font-size: 14px;
    letter-spacing: 0.5px;
  }

  @include mobile {
    padding: 15px;
  }
}
